@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700;900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@300;400;500;700&display=swap");
@import "config";
@import "color";
@import "header";
@import "animations";
@import "panel";
@import "box";
@import "media";

@import "avatar";

@import "textani";

body {
  font-family: "Roboto", sans-serif;
  height: 100%;
  line-height: 1.5;

  background-color: lightblue;
  overflow-x: hidden;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}

h1 {
  font-size: 72px;
  font-weight: 300;
}

h2 {
  font-size: 52px;
  font-weight: 600;
  text-transform: uppercase;
  line-height: 1.1;
}
h3 {
  font-size: 32px;
  font-weight: 400;
  line-height: 40px;
}
h4 {
  font-size: 34px;
  font-weight: 600;
  text-transform: uppercase;
}
h5 {
  font-size: 24px;
  font-weight: 800;
}
h6 {
  font-size: 18px;
  font-weight: 600;
}
hr.solid {
  border-top: 3px solid #bbb;
}
