// xxxxxxxxxx

.box1 {
  flex: 4 1 33.3333334%;
}
.box2 {
  flex: 1 5 33.3333334%;
  clip-path: circle(33% at center);
  background: rgba(255, 255, 255, 0.5);
  transition: all 1s ease-in-out;
  display: flex;
  align-items: center;
  justify-content: center;
}

.box3 {
  flex: 1 5 33.3333334%;
}
.box4 {
  flex: 1 0 50%;
  padding: 10px;
  position: relative;
  text-align: center;
  padding: 10px 0px;
}

.box5 {
  flex: 1 0 50%;
  padding: 10px 25px;
}
.box6 {
  flex: 1 0 25%;
}
.box7 {
  flex: 1 0 25%;
}
.box8 {
  flex: 1 0 40%;
}
.box9 {
  flex: 1 0 60%;
  min-height: 800px;
}
.box10 {
  flex: 1 0 50%;
}
.box11 {
  flex: 1 0 70%;
}
.box12 {
  flex: 1 0 70%;
}
.box13 {
  flex: 1 0 30%;
}
.box14 {
  flex: 1 0 25%;
  margin-right: 2rem;
}
.box15 {
  flex: 1 0 25%;
  margin-right: 2rem;
}
.box16 {
  flex: 1 0 25%;
}
.box17 {
  flex: 1 0 25%;
}

@media all and (max-width: 1300px) {
  .box {
    color: white;
    // font-size: 2rem;
    text-align: left;
    // text-shadow: 4px 4px 0 rgba(0, 0, 0, 0.1);
    display: flex;
    // text-transform: uppercase;
  }
  .box1 {
    flex: 1 2 50%;
    order: 1;
    background: rgba(0, 0, 0, 0.05);
  }

  .box2 {
    flex: 1 5 50%;
    .avatar {
      // background: red;
      // background: rgba(255, 255, 255, 0.4);
      clip-path: circle(43% at center);
      // transition: all 1s ease-in-out;
      img {
        width: 80%;
      }
    }
    clip-path: circle(28% at center);
    background: rgba(255, 255, 255, 0.5);
    transition: all 1s ease-in-out;
  }

  .box3 {
    flex: 1 0 40%;
    h2 {
      font-size: 1.5rem;
    }
  }
  .box4 {
    flex: 1 2 100%;
  }
  .box5 {
    flex: 1 2 100%;
    order: 5;
  }

  .box6 {
    flex: 1 2 33.33333%;
    order: 6;
  }
  .box9 {
    flex: 4 0 50%;
    justify-content: space-between;
  }
}

@media all and (max-width: 767px) {
  /* Turn on flexbox */

  .box {
    width: 100%;
  }

  //   /* Reorder items */
  .container > * {
    order: 999;
  }

  //   .box {
  //     flex-basis: 500px;
  //     flex-grow: 1;
  //   }

  .box1 {
    order: 4;
    flex: 1 2 100%;
  }

  .box2 {
    order: 2;
    flex: 1 0 100%;

    .avatar {
      // background: red;
      // background: rgba(255, 255, 255, 0.4);
      clip-path: circle(43% at center);
      // transition: all 1s ease-in-out;
      img {
        width: 80%;
      }
    }
    clip-path: circle(30% at center);
    background: rgba(255, 255, 255, 0.5);
    transition: all 1s ease-in-out;
  }
  .box3 {
    order: 3;
    flex: 1 0 100%;
    h2 {
      font-size: 2rem;
    }
  }
  .box4 {
    order: 1;
    flex: 1 0 100%;
    // img {
    //   width: 100%;
    // }
  }
  .box5 {
    order: 5;
    flex: 1 0 100%;
  }
  .box6 {
    order: 6;
    flex: 1 0 100%;
  }

  .box14 {
    flex: 1 0 100%;
  }
  .box15 {
    flex: 1 0 100%;
  }
  .box16 {
    flex: 1 0 100%;
  }
}
