.line {
  width: 100%;
  max-width: 1400px;
  height: 2px;
  margin: 0px 20px 15px 0px;
  position: relative;
  display: inline-block;
  background-color: rgba(255, 255, 255, 1);
}

.panel {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  margin: auto;
  display: flex;
  justify-content: center;
  position: relative;
  box-sizing: border-box;
  padding: 0 5vw;
  padding-bottom: 10%;
}

.container {
  // width: 90%;
  display: flex;
  min-height: 20vh;
  margin-top: 130px;
  margin-bottom: 100px;
  flex-wrap: wrap;

  border: 16px solid #fff;

  max-width: 1447px+26px;
  // max-width: 1310px;
  // background: yellow;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px;
  // margin-top: -15px;
  // margin-bottom: -15px;
  position: relative;
  // background-color: gray;
}

.padd-15 {
  padding-left: 15px;
  padding-right: 15px;
